import { getApi, getFirstBudget } from '@core'
import { useBudget } from '@hooks'
import { Box, Text } from 'ink'
import { useEffect } from 'react'
import { Loading } from '../loading/loading.tsx'

interface AccountProps {
  name: string
}

export const Account = ({ name }: AccountProps) => {
  const { budget } = useBudget({ includeAccounts: true })
  const myAccount = budget?.accounts?.find((account) => account.name === name)
  return (
    <Loading loaded={Boolean(budget)}>
      <Box>
        <Box>
          <Text>{myAccount?.name}</Text>
        </Box>
        <Box>
          <Text>{myAccount?.balance}</Text>
        </Box>
      </Box>
    </Loading>
  )
}
